* {
    font-family: 'Roboto', sans-serif;
}

.container {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
}

.calBtn {
    width: 75px;
    height: 75px;
    border-radius: 50%;
    border: none;
    font-size: 35px;
    cursor: pointer;
    outline: none;
    text-align: center;
    margin-left: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.activatedBtn {
    background-color: white;
    color: #ff9f0a;
}

.first {
    margin-left: 0px !important;
}

.orange {
    background-color: #ff9f0a;
    color: white;
}
.grey {
    background-color: #333333;
    color: white;
}
.lightGrey {
    background-color: #a5a5a5;
    color: black;
}

.playerBar {
    position: fixed;
    bottom: 0;
    left: 0;
}
.row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}
.icon {
    width: 40px;
    height: 40px;
}
.display {
    width: 100%;
    background-color: black;
    height: 70px;
    .displayText {
        font-size: 55px;
        color: white;
        text-align: right;
        padding-right: 20px;
        padding-top: 5px;
        font-family: 'Roboto', sans-serif;
        font-weight: 100;
    }
    .inputText {
        background-color: black;
        border: none;
        font-size: 55px;
        color: white;
        text-align: right;
        padding-top: 5px;
        font-family: 'Roboto', sans-serif;
        font-weight: 100;
        width: 90%;
        outline: none;

        // remove underline
        &::placeholder {
            text-decoration: none;
        }
        &:focus {
            outline: none;
        }
    }
}

.bg {
    background-color: black;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: 0;
}

.hv {
    &:hover {
        scale: 1.1;
    }
}

.circled {
    border: 4px solid white;
}
